import { Metadata } from 'next';
import { fetchJobsByCategory, getJobCount } from '@/lib/api';
import CategoryJobsPage from '@/components/CategoryJobsPage';

const CATEGORY_INFO = { title: 'Medical Science Liaison', description: 'Explore MSL roles in leading pharmaceutical companies. Bridge the gap between science and business.' };

export async function generateMetadata(): Promise<Metadata> {
  const count = await getJobCount('medical-science-liaison');
  return {
    title: `${count} Medical Science Liaison Jobs in Pharma Companies | Jobslly`,
    description: `Discover ${count} medical science liaison jobs in leading pharma companies across India. Apply now for MSL roles with Jobslly.`,
    alternates: { canonical: 'https://jobslly.com/jobs/medical-science-liaison' },
    robots: { index: true, follow: true },
  };
}

export default async function MedicalScienceLiaisonJobsPage() {
  const jobsData = await fetchJobsByCategory('medical-science-liaison', 20);
  const jobs = jobsData.jobs || [];
  const totalCount = jobsData.total || jobs.length;
  const jsonLd = { '@context': 'https://schema.org', '@type': 'ItemList', name: `${totalCount} Medical Science Liaison Jobs`, numberOfItems: totalCount, itemListElement: jobs.map((job: any, i: number) => ({ '@type': 'ListItem', position: i + 1, url: `https://jobslly.com/jobs/${job.slug}` })) };
  return <CategoryJobsPage category="medical-science-liaison" categoryTitle="Medical Science Liaison" totalCount={totalCount} description={`${totalCount} Medical Science Liaison Jobs in Pharma Companies | Jobslly`} intro={CATEGORY_INFO.description} jobs={jobs} jsonLd={jsonLd} />;
}
